import { Component, OnInit, ViewChild, ElementRef, HostListener, Inject } from "@angular/core";

import * as THREE from "three";
import { AbstractPlugin } from "../abstract-plugin";
import { PluginWrapperComponent, PluginConfig, PLUGIN_CONTEXT } from "shared";
import { takeUntil } from "rxjs/operators";

@Component({
  selector: "app-map",
  templateUrl: "./map.component.html",
  styleUrls: ["./map.component.scss"],
})
export class MapComponent implements AbstractPlugin, OnInit {
  @ViewChild("threeRef", { static: true }) threeRef: ElementRef;
  camera;
  scene;
  mesh;
  renderer;
  geometry;
  material;

  width;
  height;
  devicePixelRatio;
  constructor(private elRef: ElementRef, @Inject(PLUGIN_CONTEXT) public context: PluginWrapperComponent) {}
  // @HostListener("window:resize", ["$event"])
  resizeRender() {
    if (this.camera) {
      this.width = this.elRef.nativeElement.clientWidth;
      this.height = this.elRef.nativeElement.clientHeight;
      this.camera.aspect = this.width / this.height;
      this.camera.updateProjectionMatrix();
      this.devicePixelRatio = window.devicePixelRatio ? Math.min(1.6, window.devicePixelRatio) : 1;
      this.renderer.setPixelRatio(this.devicePixelRatio);
      this.renderer.setSize(this.width, this.height);
    }
  }
  ngOnInit(): void {
    this.init2();
    this.context.redraw$.pipe(takeUntil(this.context.destroy$)).subscribe(() => {
      this.resizeRender();
    });
  }
  init2() {
    this.camera = new THREE.PerspectiveCamera(
      70,
      this.threeRef.nativeElement.clientWidth / this.threeRef.nativeElement.clientHeight,
      0.01,
      10
    );
    this.camera.position.z = 1;

    this.scene = new THREE.Scene();

    this.geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2);
    this.material = new THREE.MeshNormalMaterial();

    this.mesh = new THREE.Mesh(this.geometry, this.material);
    this.scene.add(this.mesh);

    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
      canvas: this.threeRef.nativeElement,
    });
    this.renderer.setSize(this.elRef.nativeElement.clientWidth, this.elRef.nativeElement.clientHeight);
    // document.body.appendChild(this.renderer.domElement);
    this.animate();
  }

  animate() {
    requestAnimationFrame(this.animate.bind(this));

    this.mesh.rotation.x += 0.01;
    this.mesh.rotation.y += 0.02;

    this.renderer.render(this.scene, this.camera);
  }
}
